.Home {
    display: flex;
    justify-content: center;
}

.home-box {
    width: fit-content;
    margin-top: 1.5cm;
    display: flex;
    flex-direction: column;
   align-items: center;
}

.name {
    font-weight: 1000;
    font-size: 100px;
    font-family: changa;
    height: fit-content;
    background: linear-gradient(90deg, rgb(230, 65, 87) 0%, rgb(65, 93, 230) 100%);
    background-clip: text;
    -webkit-background-clip: text;
    color: transparent;
    margin: 0;
    padding: 0; /* Add this line to remove padding */
}

.tagline {
    font-size: 40px;
    color: rgb(50, 50, 50);
    font-family: Titillium Web;
    font-weight: 1000;
    margin-top: -1.5cm;
}
